body {
  background-color: blueviolet;
}

.container {
    margin: 0 auto;
    justify-content: center;
    background-color: chartreuse;
    text-align: center;
    font-family: sans-serif;
}

.Header {
    text-align: center;
    height: 20%;
    width: 90%;
    background-color: darkorange;
    margin: 0 auto;
}
.click-item {
    height: 200px;
    width: 200px;
    background-size: cover;
    display: inline-flex;
    margin: 10px 10px 10px 10px;
    box-shadow: 1px 1px 13px darkslategrey;
}
.click-item:hover {
    cursor: pointer;
}
